<template>
  <div class="Test">
    Test
    <button @click = 'getShext'></button>
    <div class = "moniSelect">
      <input type="text">
      <ul>
        <li v-for = 'item in optionItem' :key = "item.id" @click = 'clickOption'>{{item.optionInfo}}</li>
      </ul>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'Test',
  data () {
    return {
      msg: '',
      optionItem:[
        {
          id:1,
          optionInfo:''
        },
        {
          id:2,
          optionInfo:'2'
        },
        {
          id:3,
          optionInfo:'3'
        },
      ]
    }
  },
  methods:{
      getShext:function(){
          console.log(1);
          wx.config({
            debug: true, // ,apialertpclogpc
            appId: 'wx12fb831e0165e2df', // 
            timestamp: 1555029865, // 
            nonceStr: '1650888638', // 
            signature: 'de368033d6385647e8c150eab47612ff19fa1d15',// 
            jsApiList: ['chooseImage'] // JS
          });
          wx.ready(function(){
            console.log("");
              // configreadyconfigconfigreadyready
          });
          wx.checkJsApi({
              jsApiList: ['chooseImage'], // JSJS2,
              success: function(res) {
                  console.log(res);
              // apitruefalse
              // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
              }
          });
          wx.chooseImage({
            count: 1, // 9
            sizeType: ['original', 'compressed'], // 
            sourceType: ['album', 'camera'], // 
            success: function (res) {
              var localIds = res.localIds; // IDlocalIdimgsrc
              console.log(localIds);
            }
          });
        console.log(12);
      }
  }
}
</script>

